/*  bhoechie tab */
div.bhoechie-tab-container {
	z-index: 10;
	background-color: #ffffff;
	padding: 0 !important;
	border-radius: 4px;
	-moz-border-radius: 4px;
	border: 1px solid #ddd;
	margin-top: 20px;
	margin-left: 50px;
	-webkit-box-shadow: 0 6px 12px rgba(0, 0, 0, .175);
	box-shadow: 0 6px 12px rgba(0, 0, 0, .175);
	-moz-box-shadow: 0 6px 12px rgba(0, 0, 0, .175);
	background-clip: padding-box;
	opacity: 0.97;
	filter: alpha(opacity = 97);
	}
	div.bhoechie-tab-menu {
	padding-right: 0;
	padding-left: 0;
	padding-bottom: 0;
	background-color: rgb(146, 154, 156);
	}
	div.bhoechie-tab-menu div.list-group {
	margin-bottom: 0;
	}
	div.bhoechie-tab-menu div.list-group>a {
	margin-bottom: 0;
	}
	div.bhoechie-tab-menu div.list-group>a .glyphicon, div.bhoechie-tab-menu div.list-group>a .fa
	{
	color: #5A55A3;
	}
	div.bhoechie-tab-menu div.list-group>a:first-child {
	border-top-right-radius: 0;
	-moz-border-top-right-radius: 0;
	}
	div.bhoechie-tab-menu div.list-group>a:last-child {
	border-bottom-right-radius: 0;
	-moz-border-bottom-right-radius: 0;
	}
	div.bhoechie-tab-menu div.list-group>a.active, div.bhoechie-tab-menu div.list-group>a.active .glyphicon,
	div.bhoechie-tab-menu div.list-group>a.active .fa {
	background-color: rgb(76, 152, 240);
	background-image: rgb(76, 152, 240);
	color: #ffffff;
	}
	div.bhoechie-tab-menu div.list-group>a.active:after {
	content: '';
	position: absolute;
	left: 100%;
	top: 50%;
	margin-top: -13px;
	border-left: 0;
	border-bottom: 13px solid transparent;
	border-top: 13px solid transparent;
	border-left: 10px solid rgb(76, 152, 240);
	}
	div.bhoechie-tab-content {
	background-color: #ffffff;
	margin: 6px 20px 7px 20px;
	box-shadow: 0 4px 8px 0 rgba(0,0,0,0.2);
	transition: 0.3s;
	border: none;
	padding: 0;
	}
	div.bhoechie-tab div.bhoechie-tab-content:not(.active){
	display: none;
	}

	.grid-column-number{
		text-align: right;
	}
	.overlay-content {
		position: relative;
		top: 25%;
		width: 100%;
		text-align: center;
		margin-top: 30px;
	}
	.fullscreen{
		background-color: rgb(0,0,0);
		background-color: rgba(0,0,0, 0.9);
		z-index: 9999; 
		width: 100%; 
		height: 100%; 
		position: fixed; 
		top: 0; 
		left: 0; 
	}